

.panel {
    padding: $base-line-height-rem;
    margin-bottom: $base-line-height-rem;

    &.panel-marginless {
        margin-bottom: 0;
    }

    .panel-content {
        display: block;
        margin: 0;
        margin-bottom: -($base-line-height-rem);
        padding: 0;
    }

    &.panel-header {
        border-style: solid;
        border-width: 2px;
        border-color: #C680A4;
        border-radius: rem-calc(10);
        background-color: rgb(102,35,55);
        background-color: rgba(102,35,55,.7);
    }

    &.panel-proc-studio {
        padding-left: 0;
        padding-right: 0;
        padding-bottom: 0;
    }
}

@media #{$medium-up} {

    .panel {

        &.panel-proc-studio {
            padding-left: $base-line-height-rem;
            padding-right: $base-line-height-rem;
        }
    }
}
